import React, {useState} from "react";
import css from './ProfileInfo.module.css'
import Preloader from "../../common/Preloader/Preloader";
import userPhoto from "../../../assets/images/noimg.jpg";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import ProfileDataFormRedux, {ProfileDataForm} from "./ProfileDataForm";


const ProfileInfo = (props) => {

    let [editMode, setEditMode] = useState(false)

    if (!props.profile) {
        return <Preloader/>
    }

    const editProfile = (formData) => {
        debugger
        props.updateUserInfo({
            aboutMe: 'ХАЭЙ',
            userId: formData.userId,
            lookingForAJob: formData.lookingForAJob,
            lookingForAJobDescription: formData.lookingForAJobDescription,
            fullName: formData.fullName,
            contacts : {
                github: formData.github,
                vk: formData.vk,
                facebook: formData.facebook,
                instagram: formData.instagram,
                twitter: formData.twitter,
                website: formData.website,
                youtube: formData.youtube,
                mainLink: formData.mainLink,
            }
        })
        setEditMode(false);
    }

    const onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
            props.savePhoto(e.target.files[0]);
        }
    }
    return (

        <div>
            <img className={css.profile_header__img} src={props.header_img}/>

            <div className={css.profile_info}>
                <h2>{props.profile.fullName}</h2>
                <img className={css.profile_info__img}
                     src={props.profile.photos.large || userPhoto}/>
                {props.isOwner &&
                <input type={'file'} onChange={onMainPhotoSelected}/>}
                 <ProfileStatusWithHooks profile={props.profile} status={props.status}
                                updateStatus={props.updateStatus}/>
                {editMode ?
                    <ProfileDataFormRedux {...props} initialValues={props.profile} onSubmit={editProfile}/> : <ProfileData {...props} activateEditMode={() => {
                        setEditMode(true);
                    }}/>}
            </div>
        </div>
    )
}

const ProfileData = (props) => {
    return <div>
        {props.isOwner &&
        <button onClick={props.activateEditMode}>Изменить</button>}

        <div>
            {props.profile && props.profile.lookingForAJob ?
                <div>В поиске работы:
                    Да<br/>Описание: {props.profile.lookingForAJobDescription}
                </div> : <div>В поиске работы: Нет</div>}
        </div>
        <div>
            Контакты:{Object.keys(props.profile.contacts).map(key => {
            return <Contact contactTitle={key}
                            contactValue={props.profile.contacts[key]}/>
        })}
        </div>
    </div>
}


const Contact = ({contactTitle, contactValue}) => {
    return <div className={css.contact}>{contactTitle}: {contactValue}</div>
}

export default ProfileInfo;