import React from "react";
import {
    createFieldAdvanced,
} from "../../common/FormsControls/FormsControls";
import {reduxForm} from "redux-form";

export const ProfileDataForm = (props) => {
    return <div>

        <form onSubmit={props.handleSubmit}>
            <button>Сохранить</button><br/>
            {createFieldAdvanced('userId', props.curLoginedId, 'input', 'hidden' )}
            <label>В поиске работы:</label><br/>
            {createFieldAdvanced('lookingForAJob', props.profile.lookingForAJob, 'input', 'checkbox')}<br/>
            <label>Текст для соискателей:</label><br/>
            {createFieldAdvanced('lookingForAJobDescription','textarea')}<br/>
            <label>Полное имя</label><br/>
            {createFieldAdvanced('fullName', props.profile.fullName)}<br/>
            {Object.keys(props.profile.contacts).map((key) => {
                return <div>{key}: {createFieldAdvanced(key)}</div>
            })}
        </form>
    </div>
}
const ProfileDataFormRedux = reduxForm(
    {form: 'editProfile'
    })(ProfileDataForm);

export default ProfileDataFormRedux;